$prime: #ff8d72;
$red: #fd5d93;
$green: #00f2c3;
$font-color: #525f7f;
$body-start: #1e1e2f;
$body-end: #1e1e24;

body, html {
	background: linear-gradient($body-start, $body-end);
	font-size: 10px;
	font-family: sans-serif;
	width: 100%;
	height: 100%; }

body {
	display: flex;
	flex-flow: column;
	justify-content: center;
	align-items: center; }

.form-field {
	position: relative;
	margin: 0 0 1rem 0;
	width: 30rem;
	height: 7rem;

	input {
		background-color: darken($body-end, 5%);
		border: none;
		display: block;
		width: 24.5rem;
		padding: 1.5rem 4rem 1.5rem 1.5rem;
		border-radius: 0.5rem;
		margin: 2rem;
		position: relative;
		z-index: 1;
		font-size: 1.6rem;
		color: $font-color;
		background-repeat: no-repeat;
		background-position: 97% 50%;
		background-size: 3rem;
		background-image: url('../img/none.svg');
		transition: all 300ms; }

	label {
		font-size: 0.9rem;
		color: $prime;
		position: absolute;
		top: 2.5rem;
		left: 3.5rem;
		opacity: 1;
		z-index: 2;
		transition: all 300ms; }

	input:placeholder-shown + label {
		opacity: 0;
		font-size: 1.6rem;
		color: $font-color; }

	*::placeholder {
		color: $font-color; }

	input:focus {
		outline: none;
		background-image: url('../img/spinner.svg'); }

	input:focus:invalid + label + .hint-text::before {
		opacity: 1;
		color: $red; }

	input:focus:invalid + label + .email::before {
		content: 'Bitte gebe deine E-Mail Adresse ein.'; }

	input:focus:invalid + label + .password::before {
		content: 'Bitte gebe dein Passwort ein.'; }

	input:invalid + label {
		color: $red; }

	input:valid + label {
		color: $green; }

	input:placeholder-shown:valid {
		background-image: url('../img/none.svg');
		background-size: 2.5rem; }

	input:placeholder-shown:invalid {
		background-image: url('../img/none.svg');
		background-size: 2.5rem; }

	input:valid {
		background-image: url('../img/check.svg');
		background-size: 2.5rem; }

	input:invalid {
		background-image: url('../img/fail.svg');
		background-size: 2.5rem; }

	input:valid:focus {
		background-image: url('../img/spinner.svg');
		background-size: 3rem; }

	input:invalid:focus {
		background-image: url('../img/spinner.svg');
		background-size: 3rem; }

	.hint-text {
		position: absolute;
		bottom: -0.7rem;
		left: 2.3rem;
		width: 30rem; }

	.hint-text::before {
		opacity: 0;
		display: block;
		font-size: 1rem;
		width: 100%;
		position: absolute;
		top: 0;
		left: 0; }

	input::after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		height: 2rem;
		width: 2rem;
		background-color: $prime; } }
